:root {
  --peach: #f38e82;
  --golden: #f7ce68;
  --ivory: ivory;
  --dark-chocolate: #3b2f2f;
  --near-white: #f9f5f3;
  --primary: "Nunito Sans", sans-serif;
  --base-size: 1rem;
  --scale: 1.25;
  --h1: calc(var(--h2) * var(--scale));
  --h2: calc(var(--h3) * var(--scale));
  --h3: calc(var(--h4) * var(--scale));
  --h4: calc(var(--h5) * var(--scale));
  --h5: calc(var(--h6) * var(--scale));
  --small: calc(var(--base-size) / var(--scale));
  --h6: var(--base-size);
}

body {
  line-height: 1.5;
  font-family: var(--primary);
  font-size: var(--base-size);
  background-color: #fbab7e;
  background-image: linear-gradient(62deg, #fbab7e 0%, #f7ce68 100%);
  background-size: cover;
  min-height: 92rem;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: var(--h1);
}

h2 {
  font-size: var(--h2);
}

h3 {
  font-size: var(--h3);
}

h4 {
  font-size: var(--h4);
}

h5 {
  font-size: var(--h5);
}

h6 {
  font-size: var(--h6);
}

img {
  max-width: 100%;
}

figure {
  margin: 0;
  padding: 0;
}

.container {
  background-color: var(--ivory);
  color: var(--dark-chocolate);
  border-radius: 9px;
  grid-template: "head head" 8rem
                 "list recipe" minmax(100rem, min-content)
                 / 1fr 2fr;
  width: 1200px;
  margin: 4rem auto;
  display: grid;
  overflow: hidden;
  box-shadow: 0 2rem 6rem .5rem #61555133;
}

.header {
  background-color: var(--near-white);
  grid-area: head;
  justify-content: space-between;
  align-items: stretch;
  display: flex;
}

.header h1 {
  font-size: var(--h4);
  align-items: center;
  column-gap: .75rem;
  margin: 0 0 0 3rem;
  display: flex;
}

.header h1 svg {
  background-color: #fbab7e;
  background-image: linear-gradient(62deg, #fbab7e 0%, #f7ce68 100%);
  border-radius: 10rem;
  padding: .8rem 1rem;
}

.header .search-form {
  background-color: #fff;
  border-radius: 2rem 2rem 0 0;
  align-self: center;
  padding-left: 1rem;
  position: relative;
}

.header .search-form input {
  color: inherit;
  font-family: inherit;
  font-size: var(--base-size);
  background: none;
  border: none;
  width: 20rem;
}

.header .search-form .autocomplete-search {
  color: #f5f5f5;
  z-index: 10;
  background-color: #3b2f2f;
  width: 100%;
  height: 10rem;
  position: absolute;
  top: 100%;
  left: 0;
}

.header .search-form input:focus {
  outline: none;
}

.header .bookmark-list {
  cursor: pointer;
  align-items: center;
  column-gap: 1rem;
  margin-right: 3rem;
  transition: all .1s;
  display: flex;
}

.header .bookmark-list svg {
  color: #fbab7e;
}

.header .bookmark-list:hover {
  transform: scale(1.05);
}

.search-results {
  background-color: #fff;
  flex-direction: column;
  grid-area: list;
  padding: 2rem 0;
  display: flex;
  position: relative;
}

.search-results .copyright {
  font-size: var(--small);
  margin-top: 2.5rem;
  padding: 0 2.25rem;
}

.search-results .pagination {
  justify-content: space-between;
  margin-top: auto;
  padding: 0 2.25rem;
  display: flex;
}

.search-results .pagination button {
  color: var(--peach);
  background-color: #f9f5f3;
  background-image: none;
}

.search-results .results {
  margin: 0 0 1.25rem;
  padding: 0;
  list-style: none;
}

.search-results .results svg {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
}

.search-results .results li {
  text-transform: uppercase;
  font-size: var(--base-size);
  color: var(--peach);
  cursor: pointer;
  align-items: center;
  column-gap: 1.5rem;
  padding: 1.25rem 2.25rem;
  transition: all .1s;
  display: flex;
}

.search-results .results li:hover {
  background-color: var(--near-white);
}

.search-results .results figure {
  backface-visibility: hidden;
  border-radius: 50%;
  flex: 0 0 5.8rem;
  height: 5.8rem;
  position: relative;
  overflow: hidden;
}

.search-results .results img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: all .3s;
  display: block;
}

.search-results .results figure:before, .bookmarks .bookmark figure:before {
  content: "";
  opacity: .4;
  background-image: linear-gradient(62deg, #fbab7e 0%, #f7ce68 100%);
  border-radius: 10rem;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.recipe {
  background-color: var(--near-white);
  grid-area: recipe;
  display: grid;
  position: relative;
}

.recipe figure {
  height: 25rem;
  position: relative;
}

.recipe img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

.recipe figure:before {
  content: "";
  opacity: .6;
  background-color: #fbab7e;
  background-image: linear-gradient(62deg, #fbab7e 0%, #f7ce68 100%);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.recipe h2 {
  font-size: var(--h3);
  text-align: center;
  color: var(--ivory);
  width: 60%;
  font-weight: 700;
  line-height: 2;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 20%)skewY(-6deg);
}

.recipe .loader-svg {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
}

.recipe h2 span {
  -webkit-box-decoration-break: clone;
  box-decoration-break: clone;
  background-color: #fbab7e;
  background-image: linear-gradient(to bottom right, #fbdb89, #f48982);
  padding: 1.3rem 2rem;
}

.recipe-details {
  justify-content: space-between;
  align-items: center;
  padding: 7.5rem 4rem 3.5rem;
  display: flex;
}

.recipe-details ul {
  column-gap: 3rem;
  padding: 0;
  list-style: none;
  display: flex;
}

.recipe-details ul li, .recipe-details p {
  align-items: center;
  column-gap: .75rem;
  display: flex;
}

.recipe-details p svg {
  font-size: var(--h4);
  cursor: pointer;
}

.recipe-details ul svg {
  font-size: var(--h5);
  cursor: pointer;
}

.recipe-ingredients {
  line-height: 1.4;
  font-size: var(--h6);
  background-color: #f2efee;
  flex-direction: column;
  align-items: stretch;
  gap: 2rem;
  padding: 5rem 4rem;
  display: flex;
}

.recipe-ingredients h3 {
  font-size: var(--h4);
  color: var(--peach);
  align-self: center;
  margin: 0;
}

.recipe-ingredients ul {
  grid-template-columns: 1fr 1fr;
  gap: 2rem 2.5rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}

.recipe-ingredients ul li {
  column-gap: .75rem;
  display: flex;
}

.recipe-directions {
  flex-direction: column;
  align-items: center;
  padding: 5rem 6rem;
  display: flex;
}

.recipe-directions h3 {
  font-size: var(--h4);
  color: var(--peach);
  margin: 0;
}

.recipe-cta {
  gap: 1.5rem;
  display: flex;
}

.btn {
  text-transform: uppercase;
  cursor: pointer;
  color: var(--ivory);
  font-size: var(--base-size);
  background-image: linear-gradient(to bottom right, #fbdb89, #f48982);
  border: none;
  border-radius: 10rem;
  justify-content: center;
  align-items: center;
  padding: 1.25rem 2.25rem;
  font-weight: 600;
  text-decoration: none;
  display: flex;
}

.shimmer {
  background-color: var(--near-white);
  position: relative;
}

.shimmer svg {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
}

.bookmarks {
  grid-column: 1 / 3;
}

.bookmarks > ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.bookmarks .bookmark {
  grid-template-columns: min-content 1fr min-content;
  column-gap: 2rem;
  padding: 2rem;
  display: grid;
}

.bookmarks .bookmark h5 {
  color: var(--peach);
}

.bookmark .bookmark-icon {
  padding: 0 4rem;
  font-size: 1.5rem;
}

.bookmark .bookmark-details h5, .bookmark .bookmark-details p {
  margin: 0;
}

.bookmark .bookmark-details {
  grid-template-columns: 1fr max-content;
  gap: 1rem;
  display: grid;
}

.bookmark .bookmark-details ul, .bookmark .bookmark-details ul li {
  gap: .5rem;
  display: flex;
}

.bookmark svg {
  color: var(--peach);
}

.bookmark .bookmark-details p {
  grid-column: 1 / 3;
}

.bookmarks .bookmark-image {
  backface-visibility: hidden;
  border-radius: 50%;
  width: 10rem;
  height: 10rem;
  position: relative;
  overflow: hidden;
}

.bookmarks .bookmark-image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: all .3s;
  display: block;
}
